import interfaceurl from '@/utils/interface.js'
import navigationBar from '@/components/navigation-bar.vue' //引入自定义导航栏
export default {
	components: {
		navigationBar
	},
	data() {
		return {
			//设置导航栏样式
			navigationBarStyle: {
				iconText: '我要提现' //导航栏文字
			},
			userDetail: {}, //获取用户详情
			//提现参数
			params : {
				name: '', //姓名
				bank: '', //开户行
				bank_card: '', //银行卡号
				amount: 0 //提现金额
			}
		}
	},
	onShow() {
		this.getUserDetail()
	},
	methods: {
		//获取用户详情
		getUserDetail() {
			let that = this
			interfaceurl.checkAuth(interfaceurl.showDetail, {}).then((res) => {
				that.userDetail = res.data
			});
		},
		onSubmit() {
			if (!this.params.name) {
				uni.showToast({
					title: '请填写姓名',
					icon: 'none',
					duration: 2000
				});
				return
			}
			if (!this.params.bank) {
				uni.showToast({
					title: '请填写开户行',
					icon: 'none',
					duration: 2000
				});
				return
			}
			if (!this.params.bank_card) {
				uni.showToast({
					title: '请填写提现卡号',
					icon: 'none',
					duration: 2000
				});
				return
			}
			if (!this.params.amount) {
				uni.showToast({
					title: '请填写提现金额',
					icon: 'none',
					duration: 2000
				});
				return
			}
			if(parseFloat(this.params.amount) > parseFloat(this.userDetail.profit)) {
				uni.showToast({
					title: '提现金额不能大于可提现佣金',
					icon: 'none',
					duration: 2000
				});
				return
			}
			if (parseFloat(this.params.amount) <= 100) {
				uni.showToast({
					title: '提现金额必须大于100',
					icon: 'none',
					duration: 2000
				});
				return
			}
			let that = this
			interfaceurl.checkAuth(interfaceurl.cashOutSubmit, this.params).then((res) => {
				uni.showToast({
				    title: '提现申请已发送',
				    icon: 'none',
				    duration: 2000
				});
			});
		}
	}
}
